.form {
  background: white;
  padding: 2rem;
  position: relative;
  padding-top: 2.5rem;
  width: 20rem; }
  .form .close {
    line-height: 2rem;
    font-size: 2rem;
    position: absolute;
    right: 1rem;
    top: 0.5rem;
    cursor: pointer; }
    .form .close:hover {
      color: #868e96; }
  .form .title {
    font-size: 1.25rem;
    font-weight: 500; }
  .form .description {
    margin-top: 0.25rem; }
  .form .error {
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
    color: #fa5252;
    text-align: center;
    font-size: 0.85rem; }
  .form input {
    width: 100%;
    font-size: 1.25rem;
    margin-top: 0.5rem;
    border: none;
    border-bottom: 1px solid #dee2e6;
    padding: 0.25rem;
    outline: none;
    border-radius: 4px; }
  .form .login {
    background: #228be6;
    text-align: center;
    color: white;
    font-weight: 500;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    cursor: pointer;
    margin-top: 1rem;
    font-size: 1.25rem; }
    .form .login:hover {
      background: #339af0; }
    .form .login:active {
      background: #228be6; }
