.editor-pane {
  flex: 1 1;
  display: flex;
  flex-direction: column; }
  .editor-pane .title {
    background: #495057;
    border: none;
    outline: none;
    font-size: 1.5rem;
    padding: 1rem;
    color: white;
    font-weight: 500; }
    .editor-pane .title::-webkit-input-placeholder {
      color: rgba(255, 255, 255, 0.75); }
    .editor-pane .title::-ms-input-placeholder {
      color: rgba(255, 255, 255, 0.75); }
    .editor-pane .title::placeholder {
      color: rgba(255, 255, 255, 0.75); }
  .editor-pane .code-editor {
    flex: 1 1;
    background: #212529;
    display: flex;
    flex-direction: column; }
    .editor-pane .code-editor .CodeMirror {
      font-size: 1rem;
      flex: 1 1;
      font-family: 'D2 Coding'; }
  .editor-pane .tags {
    padding-left: 1rem;
    padding-right: 1rem;
    height: 2rem;
    background: #495057;
    display: flex;
    align-items: center; }
    .editor-pane .tags .description {
      font-size: 0.85rem;
      color: white;
      font-weight: 600;
      margin-right: 1rem; }
    .editor-pane .tags input {
      font-size: 0.85rem;
      border: none;
      flex: 1 1;
      background: none;
      outline: none;
      font-weight: 600;
      color: rgba(255, 255, 255, 0.9); }
      .editor-pane .tags input::-webkit-input-placeholder {
        color: rgba(255, 255, 255, 0.75); }
      .editor-pane .tags input::-ms-input-placeholder {
        color: rgba(255, 255, 255, 0.75); }
      .editor-pane .tags input::placeholder {
        color: rgba(255, 255, 255, 0.75); }
