@charset "UTF-8";
/* body, 타이포그래피 등의 기본 스타일 설정 */
body {
  margin: 0;
  box-sizing: border-box;
  font-family: "Noto Sans KR", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

* {
  box-sizing: inherit; }

a {
  text-decoration: inherit;
  color: inherit; }
