.editor-template .panes {
  height: calc(100vh - 4rem);
  display: flex;
  position: relative; }
  .editor-template .panes .pane {
    display: flex;
    min-width: 0;
    overflow: auto; }
  .editor-template .panes .separator {
    width: 1rem;
    height: 100%;
    position: absolute;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
    cursor: col-resize;
    background: black; }
  @media (max-width: 767px) {
    .editor-template .panes .editor {
      flex: 1 1 !important; }
    .editor-template .panes .preview, .editor-template .panes .separator {
      display: none; } }
